	computed: {
		flist() {
			const match = (d) => d.name.includes(this.LI.keyWord);

			return this.LI.data
				.filter((e) => e.children && e.children.find(match))
				.map((e) => {
					e.children = e.children.filter(match);
					return e;
				});
		},
	},

